@import url(https://fonts.googleapis.com/css?family=Poppins&display=swap);
@-webkit-keyframes bobble{0%{transform:translateY(0)}100%{transform:translateY(-1rem)}}@keyframes bobble{0%{transform:translateY(0)}100%{transform:translateY(-1rem)}}.Splash{height:100vh;width:100%;display:flex;justify-content:center;align-items:center}.Splash .splash-body{display:flex;flex-direction:column;justify-content:center;align-items:center}.Splash .expand-icon{position:absolute;bottom:2rem;cursor:pointer;-webkit-animation:bobble 1s infinite alternate;animation:bobble 1s infinite alternate}.Splash .logo-with-icons{width:20rem;height:auto}@media (min-width: 600px){.Splash .splash-body{align-items:flex-start}.Splash .logo-with-icons{width:30rem}}

body{margin:0;padding:0;font-family:'Poppins', sans-serif}

